import React from "react"
import { OptionsStyled } from "./Options.styled"
import OptionsToggle from "./OptionsToggle"
import { Switch } from "antd"
// import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
// import { faAngleUp } from "@fortawesome/pro-regular-svg-icons"
// import { faSort } from "@fortawesome/pro-solid-svg-icons"
import { H5Styled } from "./Domains.styled"
// import { H5Styled } from "./Domains.styled"

const Options = function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  let { word_hacks, phrase_hacks, com_hacks, domains_suggested } = that.props
  /*
   * View
   */
  return (
    <OptionsStyled>
        {/*
         *
         * OPTIONS (always visible)
         *
         */}
        {/*
         * LABEL    style={{ color: "#1890ff", textShadow: "1px 0px 1px rgba(0,0,0,0.01)" }}
         */}
        <H5Styled className="doms_title attention options">
          Showing
        </H5Styled>

        {/*
         * TOGGLE VIEW
         */}
        <div className="option">
          <span
            onClick={() => {
              // if !show_unavailable, meaning if currently HIDING unavailable, but will TOGGLE to SHOW it,
              // if (!that.props.show_unavailable) {
              //   that.props.io_actions.RX__search_suggestions({ only_available: false })
              // }
              that.toggle_view_option("show_unavailable")
            }}
          >
            <Switch className="Switch" size="small" checked={!!that.props.show_unavailable} />
            <span>unavailable</span>
            {/* &amp; aftermarket*/}
          </span>
        </div>
        <div className="option">
          <span
            onClick={() => {
              that.toggle_view_option("show_premium")
            }}
          >
            <Switch className="Switch" size="small" checked={!!that.props.show_premium} />
            <span>premium/ <br />aftermarket</span>
          </span>
        </div>

        {/*{!(!domains_suggested["name"] || domains_suggested["name"].length < 3) && (*/}
        {word_hacks.length > 0 && (
          <div className="option">
            <span
              onClick={() => {
                that.toggle_view_option("show_word_hacks")
              }}
            >
              <Switch className="Switch" size="small" checked={!!that.props.show_word_hacks} />
              <span>word hacks</span>
            </span>
          </div>
        )}
        {phrase_hacks.length > 0 && (
          <div className="option">
            <span
              onClick={() => {
                that.toggle_view_option("show_phrase_hacks")
              }}
            >
              <Switch className="Switch" size="small" checked={!!that.props.show_phrase_hacks} />
              <span>phrase hacks</span>
            </span>
          </div>
        )}
        {/*{com_hacks.length > 0 && !!that.props.show_expand_suggestions && (*/}
        {/*  <div className="option">*/}
        {/*    <span*/}
        {/*      onClick={() => {*/}
        {/*        that.toggle_view_option("show_com_hacks")*/}
        {/*      }}*/}
        {/*    >*/}
        {/*      <Switch className="Switch" size="small" checked={!!that.props.show_com_hacks} />*/}
        {/*      <span>".com"s</span>*/}
        {/*    </span>*/}
        {/*  </div>*/}
        {/*)}*/}
        <div className="option">
          <span
            onClick={() => {
              that.toggle_view_option("show_expand_suggestions")
            }}
          >
            <Switch className="Switch" size="small" checked={!!that.props.show_expand_suggestions} />
            <span>expand <br />suggestions</span>
          </span>
        </div>

        {/*
         * TOGGLE MORE
         */}
        <OptionsToggle that={that} />
    </OptionsStyled>
  )
}

export default Options
