import React from "react"
import PropTypes from "prop-types"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import { faPlus, faSyncAlt, faDollarSign, faCalendarExclamation } from "@fortawesome/pro-regular-svg-icons"
// import { faPlus } from "@fortawesome/pro-light-svg-icons"

const Dom = ({ that, phraseString }) => {
  let { domains_availability, input_tld, input_str, input_spellchecked } = that.props
  let phraseStringNoSpaces = phraseString.replace(/ /g, "")
  let isExact =
    phraseStringNoSpaces === input_str.replace(/ /g, "") + "." + input_tld ||
    phraseStringNoSpaces === input_spellchecked.replace(/ /g, "") + " ." + input_tld
  let code = Number(domains_availability[phraseString.replace(/ /g, "")] || 0)
  //
  // unknown
  let status = "other"
  let Icon = <FA key={status} icon={faSyncAlt} />
  //
  // unknown
  if (code === 0) {
    status = "unknown"
    Icon = <span className="textQuestion">?</span>
  }
  // unavailable
  else if (code < 8) {
    status = "unavailable"
    Icon = <span className="nDash">&ndash;</span>
  }
  // error
  else if (code === 8) {
    status = "available"
    Icon = <span className="textQuestion">*</span>
  }
  // error - need special permission
  else if (code === 9) {
    status = "available"
    Icon = <span className="textQuestion">?</span>
  }
  // available
  else if (code >= 10 && code <= 1000) {
    status = "available"
    // Icon = <FA key={1} icon={faDollarSign} className="faDollarSign" />
    Icon = <FA key={1} icon={faPlus} className="faPlus" />
  }
  // expiring
  else if (code > 100000000) {
    status = "expiring"
    Icon = <FA key={1} icon={faCalendarExclamation} />
  }
  // premium unknown
  else if (code === 10000000) {
    status = "premium"
    Icon = [
      <FA key={1} icon={faDollarSign} className="faDoubleDollarSign" />,
      <FA key={2} icon={faDollarSign} className="faDoubleDollarSign" />
    ]
    let nwords = phraseString.split(' ').length-1
    let len = phraseStringNoSpaces.length
    let idot = phraseStringNoSpaces.indexOf('.')
    let sld = phraseStringNoSpaces.substring(0,idot)
    let tld = phraseStringNoSpaces.substring(idot+1, len)
    // extra expensive:
    if ((tld==='com' || nwords===1) && len<20 || len<10) {
      Icon.push(<FA key={3} icon={faDollarSign} className="faDoubleDollarSign" />)
    }
  }
  // premium
  else if (code > 1000) {
    status = "premium"
    Icon = (
      <span>
        <FA icon={faDollarSign} className="faDoubleDollarSign" />
        <span className="price">{Math.floor(code)}</span>
      </span>
    )
  }
  let phraseArr = phraseString.split(" ")
  return (
    <div className={`dom_name status-${status} ${isExact ? "status-exact" : ""}`}>
      <span
        className={"dom_card"}
        // onClick={()=>{
        //   let form = window.document.getElementById('101DomainAffiliateForm')
        //   if (form) {
        //     form.action =
        //   }
        // }}
      >
        <span className="icon">{Icon}</span>
        <span>
          {/*
           * display spaces as kerning
           */}
          {phraseArr.map((str, i) => (
            <span key={str + i} className="word">
              {str}
            </span>
          ))}
        </span>
      </span>
    </div>
  )
}

Dom.propTypes = {
  that: PropTypes.object, // passed down from root container
  phraseString: PropTypes.string // domain string suggestion to show, with spaces
}

export default Dom
